import {
  SET_PAGE_INDEX,
  GET_PRODUCTS_PENDING,
  GET_PRODUCTS_SUCCESS,
  GET_PRODUCTS_FAILURE,
} from "../actions/actionTypes";

const initialState = {
  isLoading: false,
  error: null,
  pageIndex: 0,
  items: [],
};
const products = (state = initialState, { type, payload }) => {
  switch (type) {
    case GET_PRODUCTS_PENDING: // gerer l'tat en chargement
      return {
        ...state,
        isLoading: true,
      };
    case GET_PRODUCTS_SUCCESS: // gerer l'tat en cas de succs
      return {
        ...state,
        isLoading: false,
        items: payload.data,
      };
    case GET_PRODUCTS_FAILURE: // gerer l'tat en cas de problme
      return {
        ...state,
        isLoading: false,
        error: payload.error,
      };
    case SET_PAGE_INDEX: // segmenter en faisant une pagination
      return {
        ...state,
        pageIndex: payload.index,
      };
    default:
      return state;
  }
};
export default products;
